.app {
  text-align: center;
  height: 100vh;
  width: 100vw;
}

 .darkTheme{
   background-color: black;
color: white;
  }
.lightTheme{
  background-color: #fff;
  color: black;
}
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}
 

  input, button {
     background-color: rgb(0, 0, 0);
     color: white;
  padding: 15px;
  margin: 20px;
 font-size: 16px;
 font-family: poppins ;
 border-radius: 20px;
 border: 0px solid #000;
 -webkit-border-radius: 20px;
 -moz-border-radius: 20px;
 -ms-border-radius: 20px;
 -o-border-radius: 20px;
}

button {
 cursor: pointer;
}
.list li button{
  padding: 3px 6px;
  font-size: 14px;
  border-radius: 4px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  -ms-border-radius: 4px;
  -o-border-radius: 4px;
}
.head{
  display: flex;
  justify-content: space-between;
  padding: 10px 30px;
}
.head button{
  margin: 0px;
}
.darkTheme form input{
  color: black;
  background-color: #fff;
}